: 0;\n  right: 0;\n}\n\n.bottom-left {\n  bottom: 0;\n  left: 0;\n}\n\n.bottom-right {\n  bottom: 0;\n  right: 0;\n}\n\n.center-overlay {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 300px; /* Arbitrary size for visibility */\n  height: 200px;\n  background-color: rgba(255, 255, 255, 0.5); /* Semi-transparent white */\n}\n\n.content {\n  padding: 20px;\n  color: white; /* Ensures text is visible */\n}\n</style>"],"sourceRoot":""}]);
